import fetch from 'utils/fetch';

export function getListRechargeCommisionByManager(data) {
	// 参数 month=201706&ctId=1&page=1&num=10
	return fetch({
	  url: '/recharge/manager/listRechargeCommisionByManager.do',
	  method: 'get',
	 data:data
	});

}

//管理员获取平台充值与厂商结算数据
export function getListRechargeStatementByManager(data) {
	return fetch({
		url:'/recharge/manager/listRechargeStatementByManager.do',
		method:'get',
		data:data
	})
}

//获取平台充值与厂商结算详单
