<template>
	<div class="app-container">
		<p>
			<el-col :span="8">
				选择月份：
				<el-date-picker v-model="month" filters type="month" format="yyyy-MM" @change="dateChange" placeholder="选择月" :clearable="false">
				</el-date-picker>
			</el-col>
		</p>
		<el-table :data="tableData" border style="width: 100%" v-loading="isLoad">
			<el-table-column prop="time" label="收支月份" width="150" align="center">
			</el-table-column>
			<el-table-column prop="contractNumber" label="合同编号" width="80" align="center">
			</el-table-column>
			<el-table-column label="收入" width="150" align="center">
				<el-table-column prop="grossPrice" label="充值流水(元)"></el-table-column>
				<el-table-column prop="customerRatio" label="分成比例(%)"></el-table-column>
			</el-table-column>
			<el-table-column label="支出">
				<el-table-column prop="packagePrice" label="流量包成本合计(元)"></el-table-column>
				<el-table-column prop="managerPrice" label="管理费合计(元)"></el-table-column>
			</el-table-column>
			<el-table-column prop="total" label="总收入(元)"></el-table-column>
		</el-table>
		<div style="text-align:center;margin: 20px auto;">
			<el-pagination style="display: inline-block" :page-size="10" @current-change="pageChange" :current-page.sync="currentPage" layout="prev, pager, next, jumper" :total="tableCount">
			</el-pagination>
		</div>
	</div>
</template>


<script>
import { listRechargeStatement } from 'api/admin/financialSettlement'
import { backCN } from 'utils/index';
export default {
	data() {
		return {
			time: '',
			contractNumber: '',
			grossPrice: '',
			customerRatio: '',
			packagePrice: '',
			managerPrice: '',
			num: 10,
			tableData: [],
			currentPage: 1,
			tableCount: 0,
			currentMonth: '201706',
			month: '2017-06',
			isLoad:true
		}
	},
	watch: {},
	mounted() {
		this.getlistRechargeStatement('201706', 1, this.num)
	},
	methods: {
		dateChange(val) {
			this.currentMonth = val.split('-').join('')
			this.getlistRechargeStatement(this.currentMonth, 1, this.num)
		},
		pageChange(page) {
			this.getlistRechargeStatement(this.currentMonth, page, this.num)
		}, 
		getlistRechargeStatement(month, page, num) {
			this.isLoad=true
			listRechargeStatement({ month, page, num }).then(res => {
				this.isLoad=false
				if (res.msg === 0) {
					for(let i in res.statementsList){
						res.statementsList[i].customerRatio=res.statementsList[i].customerRatio=(1-res.statementsList[i].customerRatio)*100
						res.statementsList[i].total=((res.statementsList[i].grossPrice - res.statementsList[i].packagePrice) * res.statementsList[i].customerRatio/100).toFixed(2)
					}
					this.tableData = res.statementsList;
					page === 1 ? this.tableCount = res.count : ''
				} else {
					this.tableData = []
					this.tableCount = 0
				}
				(res.msg != 0 && res.msg != 1) ? this.$message.error(backCN(res.code)) : ''
			}).catch(err=>{
				this.isLoad=false
				this.$message.error(backCN(err.status))
			})
		}

	}
};
</script>

<style scoped>
.app-container {
	background: #f5f5f5;
}
</style>



